body {
  width: 100%;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

body .banner {
  max-width: 1920px;
  min-height: 980px;
  box-sizing: border-box;
  width: 100%;
  background-image: url("mainimage.9c4ed44f.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  padding: 20px 200px;
  display: flex;
}

body .banner__header {
  height: 50px;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

body .banner__header__nav a {
  text-align: left;
  color: #fff;
  cursor: pointer;
  margin-left: 32px;
  font-family: Raleway, serif;
  font-size: 18px;
  font-weight: bold;
  line-height: 21px;
  text-decoration: none;
}

body .banner__header__button {
  width: 267px;
  height: 51px;
  color: #fff;
  cursor: pointer;
  background-color: #e1963c;
  border: none;
  border-radius: 30px;
  font-size: 18px;
  line-height: 21px;
}

body .banner__title {
  text-align: left;
  color: #fff;
  text-shadow: 1px 0 10px #000;
  max-width: 940px;
  margin-top: 200px;
  font-family: Playfair Display, serif;
  font-size: 100px;
  font-weight: bold;
  line-height: 133px;
}

body .banner form {
  flex-direction: row;
  margin-top: 36px;
  display: flex;
}

body .banner input {
  text-shadow: 1px 0 10px #000;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  height: 60px;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  background-color: #c8c8c880;
  border: none;
  border-radius: 15px;
  margin-right: 25px;
  padding: 0 10px;
  font: bold 1rem Raleway, sans-serif;
}

body .banner input[type="date"]::-webkit-calendar-picker-indicator {
  color: #0000;
  opacity: 1;
  width: 24px;
  height: 24px;
  background: url("calendar.8c714522.png") 0 0 / contain no-repeat;
  border-width: thin;
  display: block;
}

body .banner input[type="date"]:before {
  color: #fff;
  margin-right: .5em;
  content: attr(placeholder) !important;
}

body .banner input[type="date"]:focus:before, body .banner input[type="date"]:valid:before {
  content: "";
}

body .banner input[type="number"] {
  appearance: textfield;
}

body .banner input[type="number"]::-webkit-outer-spin-button, body .banner input[type="number"]::-webkit-inner-spin-button {
  display: none;
}

body .banner #checkin, body .banner #checkout {
  width: 360px;
}

body .banner #guests {
  width: 252px;
}

body .banner #guests::-webkit-input-placeholder {
  color: #fff;
}

body .banner #guests::-moz-placeholder {
  color: #fff;
}

body .banner button {
  width: 280px;
  height: 60px;
  color: #fff;
  cursor: pointer;
  background-color: #e1963c;
  border: none;
  border-radius: 12px;
  margin-left: 100px;
  font-size: 18px;
  line-height: 21px;
}

body .banner__circle {
  height: 60px;
  width: 60px;
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  cursor: pointer;
  z-index: 1;
  background-color: #c8c8c880;
  border-radius: 50%;
  justify-content: center;
  align-self: center;
  align-items: center;
  margin-top: 260px;
  display: flex;
  position: relative;
}

body .banner__circle img {
  height: 30px;
  width: 30px;
  z-index: 2;
  color: #000;
  filter: invert();
  position: relative;
}

